@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;600&display=swap');

*, *::before, *::after{
   box-sizing: border-box;
  }
  
*{
   margin: 0;
 }
::-webkit-scrollbar{
  display: none;
}
body{
	font-family:'Nunito', sans-serif;
}
.home{
	background: #ffffff;
  display: grid;
  grid-template-columns: [first] 20fr [line2] 30fr [line3] 50fr [end];
  gap: 1rem;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}
